.container {
  width: 1200px;
  flex-direction: column;
  justify-content: center;
  margin: 0;
  display: flex;
}

header {
  width: 100vw;
  height: 230px;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("imag-header.b608e0f1.jpg"), linear-gradient(rgba(5, 7, 12, .75), rgba(5, 7, 12, .75));
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.controls {
  width: 100%;
  height: 100px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  justify-content: center;
  align-items: center;
  margin-left: 5%;
  display: flex;
}

.logo__icon {
  width: 24px;
  height: 24px;
  margin-right: 10px;
}

.logo__icon use {
  width: 24px;
  height: 24px;
  fill: #818181;
}

.logo__text {
  width: 141px;
  height: 35px;
  text-align: center;
  color: #fff;
  align-items: center;
  font-family: Roboto;
  font-size: 30px;
  font-style: normal;
  font-weight: 500;
  line-height: 35px;
  text-decoration: none;
  display: flex;
}

.menu-home {
  width: 200px;
  height: 14px;
  justify-content: space-between;
  align-items: center;
  margin-right: 5%;
  display: flex;
  position: relative;
}

.menu-home .menu-home__item1:after {
  content: "";
  width: 35px;
  height: 3px;
  opacity: 0;
  background-color: #ff6b08;
  position: absolute;
  top: 100%;
  left: 0;
}

.menu-home .menu-home__item2:after {
  content: "";
  width: 70px;
  height: 3px;
  opacity: 0;
  background-color: #ff6b08;
  position: absolute;
  top: 100%;
  right: 18px;
}

.selected:after, .menu-home .menu-home__item1:hover:after, .selected:after, .menu-home .menu-home__item2:hover:after {
  opacity: 1;
}

.menu-home__item1 {
  width: 35px;
  height: 14px;
  cursor: pointer;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
}

.menu-home__item2 {
  width: 88px;
  height: 14px;
  cursor: pointer;
  text-align: left;
  text-transform: uppercase;
  color: #fff;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
}

.menu {
  width: 250px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.search {
  width: 336px;
  color: #fff;
  background: none;
  border: none;
  border-bottom: .5px solid #fff;
  outline: 0;
  padding-left: 2px;
}

.send-form {
  width: 100%;
  right: 0;
}

.send {
  width: 20px;
  height: 20px;
  cursor: pointer;
  background: none;
  border: none;
  position: absolute;
  top: 0;
  right: -46px;
}

.send__icon {
  width: 12px;
  height: 12px;
  background: none;
  border: none;
}

.header2 {
  width: 100vw;
  height: 230px;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("header2.39ad9a6a.jpg"), linear-gradient(rgba(5, 7, 12, .56), rgba(5, 7, 12, .56));
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.menu2 {
  width: 300px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.menu2__button {
  box-sizing: border-box;
  width: 136px;
  height: 44px;
  text-transform: uppercase;
  color: #fff;
  background: none;
  border: 1px solid #fff;
  border-radius: 5px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
}

.menu2__button:hover {
  background-color: #ff6b08;
}

@media screen and (max-width: 1200px) {
  .container {
    width: 100vw;
  }
}

@media screen and (max-width: 768.5px) {
  .container {
    width: 100vw;
    flex-direction: column;
    justify-content: center;
    display: flex;
  }

  img {
    width: 40%;
    height: 398px;
  }

  header, .controls {
    width: 100vw;
  }

  .menu-home {
    margin-right: 5%;
  }

  .search {
    width: 150%;
  }

  .menu {
    margin-left: -15%;
  }

  .movies-container {
    grid-template-columns: repeat(2, 1fr);
    display: grid;
  }
}

@media screen and (max-width: 320px) {
  .container {
    width: 100%;
    flex-direction: column;
    justify-content: center;
    display: flex;
  }

  img {
    width: 280px;
    height: 398px;
  }

  .controls {
    width: 100%;
  }

  header {
    background-position: center;
    background-size: cover;
  }

  .logo__text {
    visibility: hidden;
  }

  .logo__icon {
    margin-left: 5%;
  }

  .menu {
    margin-left: -30%;
  }

  .menu2__button {
    min-width: 130px;
  }

  .menu-home__item1 {
    margin-right: 10%;
  }

  .movies-container {
    grid-template-columns: repeat(1, 1fr);
    display: grid;
  }

  .footer__text {
    font-size: 14px;
    font-weight: 400;
    line-height: 16px;
  }
}

